import '../Style/Video.css'
import { useState } from 'react';
import { useEffect } from 'react';
const Video = ({infoVideo,infoEpsode}) => {
    console.log({infoVideo,infoEpsode})
    const [urlbase] = useState("https://player.uauflix.online/tv/");    
    const[urlSerie,seturlSerie] = useState('');   
    const[urlFilme] = useState("https://player.uauflix.online/"+infoVideo.url);
    useEffect(() => {
        if(infoEpsode){
                seturlSerie(infoEpsode[0])
            if(localStorage.getItem(infoVideo.nome)){
                let ler =JSON.parse(localStorage.getItem(infoVideo.nome))
                seturlSerie(infoEpsode[ler])
            } 
        }
        console.log(urlSerie)
    }, [urlbase,infoVideo,infoEpsode]);  
     
    const contador = (props)=>{
        let contar = 0
        let rever = JSON.parse(localStorage.getItem(infoVideo.nome))
        contar = rever+props
        if(contar<0){contar =0}
        else if(contar>infoEpsode.length-1){contar = infoEpsode.length-1}
        seturlSerie(infoEpsode[contar])
        localStorage.setItem(infoVideo.nome, JSON.stringify(contar)) 
    }  
    if(!infoEpsode){
        return(
            <div className='Video'>
            <div className='Comands' id='comando'>
            <a href='/Mflix/' className='button' >Home</a>
            </div>
            <iframe rel="preload" autoPlay  allow="autoplay; encrypted-media; preload" preload='auto' sandbox="allow-scripts  allow-same-origin" title="Mflix" allowFullScreen scrolling="no" frameBorder="0" src={urlFilme}></iframe>
            
            </div>
            )
        }
        else{
            return(                
                <div className='Video'>
                <div className='Comands'>
                <a href='/Mflix/' >Home</a>
                <select value={JSON.stringify(urlSerie)} onChange={(on)=>{
                    let value =  JSON.parse(on.target.value);
                     let busca = infoEpsode.findIndex(x=>x.name== value.name) ; 
                     seturlSerie(infoEpsode[busca]);
                     localStorage.setItem(infoVideo.nome, JSON.stringify(busca))
                     console.log(busca);
                     }}>
                {infoEpsode.map((x)=>{
                    return(
                    <option value={JSON.stringify(x)}>{x.name}</option>) 
                    })}
                </select>
                <button  type='button' onClick={()=>{contador(-1)}}>Back</button>
                <button type='button' onClick={()=>{contador(1)}}>Next</button>                
                </div>                
                {urlSerie && <iframe rel="preload" autoPlay allow="autoplay; encrypted-media; preload" preload='auto'  sandbox="allow-scripts  allow-same-origin" title="Mflix" allowFullScreen scrolling="no" frameBorder="0" src={urlbase+infoVideo.url+urlSerie.url}></iframe>
                }
                </div>
                )
            }     
        }
        export default Video;