import React from 'react';
import '../Style/Cards.css'
import ArrowBackIosIcon from '@material-ui/icons/ArrowBackIos';
import ArrowForwardIosIcon from '@material-ui/icons/ArrowForwardIos';
import { useState } from 'react';
import { useEffect } from 'react';
const Cards = ({Play, infos}) =>{   
    const [scrollX, setScrollX] = useState(-0)
    const [largura,setlargura] = useState(null)
    const [altura,setAltura] = useState(null)
    const [UrlBase] = useState('https://image.tmdb.org/t/p/w300')
    const [tittle, setTittle] = useState(false)
    
    useEffect(() => {
        if(window.innerWidth<650){
            setlargura(110)
            setAltura(110*1.48)
        }
        else{
            setlargura(160)
            setAltura(160*1.48)
        }
        if(infos && infos[0] && infos[0].genre){
            setTittle(infos[0].genre)
        }
        
    },[]);
    
    const handleLeftArrow = () =>{
        let x = scrollX + Math.round(window.innerWidth/2);
        if(x>0){x = 0;}
        setScrollX(x)
    }
    const handleRightArrow = (e) =>{
        let x = 0
        let tamanhoMax = -e*largura+window.innerWidth-60
        if( tamanhoMax < scrollX)
        { 
            if((scrollX - tamanhoMax) < window.innerWidth){x = tamanhoMax}
            else{x = scrollX - window.innerWidth*1/2}
        }
        else{x = tamanhoMax}
        setScrollX(x)
    }
    
    if(infos){
        return(
            largura && altura &&
            <>  
            <section  className={`movieRow`}>
            <h2>{tittle}</h2>
            <div className='movieRow--left' style={{height: altura}}  onClick={()=>{handleLeftArrow()}}>
            <ArrowBackIosIcon style={{fontSize:50}} />
            </div>
            <div className='movieRow--right' style={{height: altura}}  onClick={()=>{handleRightArrow(infos.length)}}>
            <ArrowForwardIosIcon style={{fontSize:50}} />
            </div>
            <div className={`movieRow--listarea`} style={{
                overflowX: "hidden",
                transition: "all ease .5s",
                marginLeft: scrollX,
            }}>                  
            <div className='movieRow--list' style={{width: largura*infos.length}}>
            {   
                infos.map((x,key)=>{
                    return(
                        x.poster &&
                        <div key={key} className='movieRow--item'>
                        <img src={UrlBase+x.poster} style={{width: largura, height: altura}} alt={x.nome} onClick={()=>{Play(x)}}/>
                        <h1>{x.rating}</h1>
                        </div>
                        )
                    })
                }
                </div>
                </div>                        
                </section>                 
                </>         
                )  
            }
            else{<div></div>}
        }
        export default Cards;