import '../Style/App.css';
import React from 'react';
import Cards from './Cards'
import Video from './Video'
import LastView from './LastView'
import { useState } from 'react';
import Header from './Header';

const Filmes = ({infos,alternarPages}) => {
  const[infoVideo,SetInfoVideo] = useState(false)
  const[exibir,setExibir] = useState(false) 
  const Play = (props) => {
    console.log('props: ',props)
    SetInfoVideo(props)
    setExibir(true)
    localStorage.setItem("LastView-movie", JSON.stringify(props))
  }
      if(infos){
        return (   
          <div className="App" >
          {!exibir &&<Header alternarPages={alternarPages} /> }
          {exibir && 
            <Video infoVideo={infoVideo} infoEpsode={null}/>
          }
          {!exibir &&
            <LastView Play={Play} infos={infos} type={'movie'}/>
          }
          {!exibir &&
            infos.map((x,i)=>{return(<Cards lista={i} key={i} Play={Play} infos={x}/>)})
          }    
          </div>
          )
      }
    else{
      return(
          <img src='https://minuta.gestaourbana.prefeitura.sp.gov.br/piu-setor-central/static/galeria/carregando.gif' alt={'carregando'} 
          style={{
            filter: 'invert(1)',
            objectFit: 'cover',
            width:'100%',
            height:'100vh',
            display: 'absolute',
            top:'0px',
            left:'0px',
            zIndex:'9999'
          }} /> 
      )
    }
}

 
  export default Filmes;