import './Style/App.css';
import React from 'react';
import { useEffect } from 'react';
import { useState } from 'react';
import Series from './Components/Series';
import Filmes from './Components/Filmes';
import {BiliotecaFilmesOrder} from './Dados/BibliotecaFilmeOrder'
import {BibliotecaSerieOrder} from './Dados/BibliotecaSerieOrder'
import Tmdb from './Components/Tmdb.js';
const App = () => {
  const[infosSeries,setnfosSeries] = useState(null)
  const[infosMovie,setnfosMovie] = useState(null)
  const[pageOn,setPageOn] = useState(true)
    
    const alternarPages =(props)=>{
      setPageOn(props)
    }

    useEffect(() => {
        setnfosSeries(BibliotecaSerieOrder)
        setnfosMovie(BiliotecaFilmesOrder)
        //Tmdb()
    },[]);
   
    return (
      <div className="App">
      
      {pageOn &&
        <Series infos={infosSeries} alternarPages={alternarPages} />
      }
      {!pageOn &&
        <Filmes infos={infosMovie} alternarPages={alternarPages}  />
      }     
      </div>
      )
    }
    export default App;