import '../Style/App.css';
import React from 'react';
import Cards from './Cards'
import Video from './Video'
import LastView from './LastView'
import { useState } from 'react';
import Header from './Header';
const Series = ({infos,alternarPages}) => {
  
  const[infoVideo,SetInfoVideo] = useState('')
  const[infoEpsode,SetInfoEpsode] = useState('') 
  const[exibir,setExibir] = useState(false)
  
  const Play = (props) => {    
    localStorage.setItem("LastView-tv", JSON.stringify(props))
    const infoEp = numberEpisodes(props)
    SetInfoVideo(props)
    SetInfoEpsode(infoEp)
    setExibir(true)
  }
  const numberEpisodes = (props)=> 
  {
    let ArryEpsodes = props.numberEpisodes
    console.log({ArryEpsodes})
    let urls = [];
    for(let i = 0; i<ArryEpsodes.length;i++){
      let b =''
      let x = ArryEpsodes[i]
      for(let z = 1; z <= x; z++){
        b = ("/" + (i+1) + "/" + z + "/dub")
        let c1 = '';
        let c2 = '';
        if(i<=9)    {c1= 'S0'+(i+1)}
        else        {c1= 'S'+(i+1)}
        if(z<=9)    {c2= "/0"+(z)+'EP'}
        else        {c2= "/"+(z)+'EP'}
        let c = c1 + c2
        urls.push({name:c,url:b})
      }
    }
    console.log({urls})
    return urls
  }
  if(infos){
    return (   
      <div className="App" > 
      {!exibir &&<Header alternarPages={alternarPages} /> }
      {exibir &&
        <Video infoVideo={infoVideo} infoEpsode={infoEpsode}/>
      }{!exibir &&
        <LastView Play={Play} infos={infos} type={'tv'}/>
      }{!exibir &&
        infos.map((x,i)=>{return(<Cards lista={i} key={i} Play={Play} infos={x}/>)})
      }
      </div>
      )
  }
else{
  return(
<img src='https://minuta.gestaourbana.prefeitura.sp.gov.br/piu-setor-central/static/galeria/carregando.gif' alt={'carregando'} 
    style={{
      filter: 'invert(1)',
      objectFit: 'cover',
      width:'100%',
      height:'99.49vh',
      display: 'absolute',
      top:'0px',
      left:'0px'
    }} />
  )
}
    
  }
  export default Series;